body {
  margin: 0;
  font-family: 'Metropolis', 'NanumSquare';
  font-weight: 500;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hljs {
  border-radius: .5rem;
}
.posts {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.posts div.post {
  width: calc(100% - 2px);
  display: flex;
  border-bottom: 1px solid #e2e2e2;
}

.posts div.post a, .posts div.post.title span {
  text-decoration: none;
  color: black;
}

.posts div.post.title span {
  font-weight: bold;
}

.postTitle {
  width: calc(75% - 2rem);
  padding: .7rem 1rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.postAuthor {
  width: calc(15% - 2rem);
  padding: .7rem 1rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.postViews {
  width: calc(10% - 2rem);
  padding: .7rem 1rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.postList {
  display: flex;
  flex-direction: column-reverse;
}

.postList .noposts {
  color: #646464;
  margin: 2rem auto;
}

.commentCount {
  margin-left: .3rem;
  color: #007eff;
}

@media (max-width: 720px) {
  .postTitle {
    width: calc(60% - 2rem);
  }
  .postAuthor {
    width: calc(30% - 2rem);
  }
}
.View .back {
  margin: 0 1rem 1rem 0;
  display: inline-block;
}

.View .top {
  display: flex;
  justify-content: center;
  align-items: baseline;
}

.View .title {
  font-size: 1.7rem;
  font-weight: 800;
}

.View .author {
  font-weight: 600;
  margin-left: .7rem;
  margin-right: auto;
}

.View .content {
  margin: 1rem 0;
}

hr {
  border-color: #e2e2e2;
  border-top: 0;
}

.button.comment {
  margin-left: 0;
}

.View .flex {
  display: flex;
  margin-bottom: 1rem;
  flex-direction: row;
}

.View input[type="text"] {
  border: 1px solid #e2e2e2;
  border-radius: .4rem;
  font-size: 1rem;
  padding: .3rem .5rem;
  margin-right: 1rem;
  width: 100%;
}

.commentList {
  display: flex;
  flex-direction: column-reverse;
}

.commentUsername {
  font-weight: 800;
  margin-right: .5rem;
  font-size: 1.2rem;
}

.commentContent {
  font-size: 1.2rem;
}

.code {
  height: 50%;
  margin: .5rem 0;
  border: 1px solid #e2e2e2;
  border-radius: .3rem;
  padding: .5rem;
  display: flex;
  background-color: #f5f5f5;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  font-size: .8rem;
}
.Login main {
  position: fixed;
  z-index: 5;
  background-color: white;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  width: calc(30vw - 3rem);
  border-radius: .5rem;
  padding: 1rem 1.5rem;
}

.Login main h2 {
  margin: 0;
}

.Login .dim {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background-color: #0000007a;
}

.Login form .inputs {
  display: flex;
  flex-direction: column;
  margin-bottom: .3rem;
}

.Login form .inputs label {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: .3rem 0;
}

input:focus {
  outline: none;
}

.Login input[type="text"],
.Login input[type="password"],
.Login input[type="email"] {
  margin-left: auto !important;
  border: 1px solid #e2e2e2;
  border-radius: .4rem;
  font-size: 1rem;
  padding: .3rem .5rem;
  width: calc(30vw - 9rem) !important;
}

.Login .banner {
  width: 100%;
  border-radius: .5rem;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

@media (max-width: 1280px) {
  .Login main {
    width: calc(50vw - 3rem);
  }
  .Login input[type="text"],
  .Login input[type="password"],
  .Login input[type="email"] {
    width: calc(50vw - 9rem) !important;
  }
}

@media (max-width: 960px) {
  .Login main {
    width: calc(70vw - 3rem);
  }
  .Login input[type="text"],
  .Login input[type="password"],
  .Login input[type="email"] {
    width: calc(70vw - 9rem) !important;
  }
}

@media (max-width: 720px) {
  .Login main {
    width: calc(90vw - 3rem);
  }
  .Login input[type="text"],
  .Login input[type="password"],
  .Login input[type="email"] {
    width: calc(90vw - 9rem) !important;
  }
}

@media (max-width: 480px) {
  .Login main {
    width: calc(95vw - 3rem);
  }
  .Login input[type="text"],
  .Login input[type="password"],
  .Login input[type="email"] {
    width: calc(95vw - 9rem) !important;
  }
}
.App form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.App label {
  display: flex;
  margin: .3rem 0;
}

.App input[type="text"],
.App textarea {
  margin-left: auto !important;
  border: 1px solid #e2e2e2;
  border-radius: .4rem;
  font-size: 1rem;
  padding: .3rem .5rem;
  width: calc(100% - 4rem);
}

.App textarea {
  height: 40vh;
  resize: vertical;
}

.App textarea:focus {
  outline: none;
}
.App {
  width: 50vw;
  margin: 0 auto;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
}

button, .button {
  background-color: transparent;
  border: 1px solid #007eff;
  display: flex;
  padding: .3rem 1rem;
  font-size: 1rem;
  border-radius: .4rem;
  color: #007eff;
  transition: .5s;
  cursor: pointer;
  margin-left: auto;
  text-decoration: none;
}

button:hover, .button:hover {
  background-color: #007eff;
  color: white;
}

button:focus, .button:focus {
  outline: none;
  background-color: #007eff;
  color: white;
}

@media (max-width: 1280px) {
  .App {
    width: 70vw;
  }
}

@media (max-width: 720px) {
  .App {
    width: 90vw;
  }
}

@media (max-width: 480px) {
  .App {
    width: 95vw;
  }
}
